<template>
  <div>
    <RightTitle title="IT风暴"></RightTitle>

    <!-- 搜索表单 -->
    <div class="uar_form">
      <el-form ref="form" :inline="true" :model="filter" label-width="80px" size="medium">
        <el-form-item label="标题">
          <el-input v-model="filter.title" clearable placeholder="搜索标题"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">搜索</el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="addTemplate">添加IT风暴</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 数据表格 -->
    <div class="uar_form">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column label="标题" prop="title" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="分类" prop="type" width="80" align="center"></el-table-column>
        <el-table-column label="时间" prop="time" width="120" align="center"></el-table-column>
        <el-table-column label="状态" prop="status" width="100" align="center"></el-table-column>
        <el-table-column label="操作" width="180" align="center">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip content="查看详情">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  size="small"
                  @click="showInfo(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip content="编辑">
                <el-button type="primary" icon="el-icon-edit" size="small"></el-button>
              </el-tooltip>
              <el-tooltip content="删除">
                <el-button
                  type="primary"
                  icon="el-icon-delete"
                  size="small"
                  @click="deleteArt(scope.row)"
                ></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="uar_form page_area">
      <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
    </div>

    <!-- 表单内容 -->
    <el-dialog :title="dialogTitle" :visible.sync="formVisible" width="40%">
      <ItstormForm :artId="infoArtId"></ItstormForm>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 查看详情 -->
    <el-dialog :title="dialogTitle" :visible.sync="infoVisible" width="40%">
      <ItstormInfo :artId="infoArtId"></ItstormInfo>
    </el-dialog>
  </div>
</template>

<script>
import RightTitle from "../../../components/RightTitle";
import ItstormForm from "./components/ItstormForm";
import ItstormInfo from "./components/ItstormInfo";
export default {
  name: "course_itstorm",
  components: {
    RightTitle,
    ItstormForm,
    ItstormInfo
  },
  data() {
    return {
      filter: {
        title: ""
      },
      tableData: [
        {
          id: 1,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        },
        {
          id: 2,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        }
      ],
      formVisible: false,
      infoVisible: false,
      dialogTitle: "添加IT风暴",
      infoArtId: 0
    };
  },
  methods: {
    //   添加
    addTemplate() {
      this.formVisible = true;
      this.dialogTitle = "添加IT风暴";
    },
    showInfo(row) {
      this.infoVisible = true;
      this.dialogTitle = "文章详情";
      this.infoArtId = row.id;
    },
    deleteArt(row) {
      this.$confirm("确定要删除吗？", "提示", {
        confirmButtonText: "删除",
        cancelButtonText: "不删除",
        type: "warning"
      })
        .then(() => {
          console.log(row);
        })
        .catch(() => {});
    }
  }
};
</script>

<style scoped>
.space {
  margin-right: 5px;
}
</style>